.list {
    color: rgba(255, 255, 255, 0.9);
    ;
    background-color: #b82f6a;
    box-shadow:
        rgba(0, 0, 0, 0.4) 3px 3px 7px 0px;
    width: 300px;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}

.list p {
    padding: 10px;
    position: relative;
}

.list p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicons {
    margin: 0 5px;
    font-size: 15px;
}

.list p input {
    background-color: transparent;
    border: 0;
    color: rgba(255, 255, 255, 0.9);
    font-size: 18px;
}

.list p input:focus {
    outline: none;
}