body {
  background-attachment: fixed;
  background-repeat: no-repeat;
  background: linear-gradient(to right, #74ebd5, #9face6);
}

.App {
  background-color: #29415e;
  box-shadow:
    rgba(0, 0, 0, 0.4) 3px 3px 5px 0px;
  border-radius: 10px;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input {
  background-color: rgba(27, 112, 137, 1);
  box-shadow:
    rgba(0, 0, 0, 0.4) 3px 3px 10px 0px inset;
  border: 0;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
}

#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

#to-do-form input:focus,
#to-do-form button:focus {
  outline: none;
}

#to-do-form button {
  box-shadow:
    rgba(24, 24, 24, 0.4) 3px 5px 10px 1px;
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(215, 164, 24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}

#to-do-form button:active {
  box-shadow:
    rgba(24, 24, 24, 0.6) 2px 2px 5px 0px inset;
}